import {
  selectListGroup,
  selectSceneSelected,
} from '@containers/Home/selectors';
import { I_Scene } from '@containers/Home/types';
import { Popover } from 'antd';
import React, { memo, useMemo } from 'react';
import { useSelector } from 'react-redux';
import { useMobileDetect } from '@containers/Home/hook/useDetectMobile';
import dynamic from 'next/dynamic';
import { HeaderWrapper } from './styled';

const Logo = dynamic(() => import('@containers/Home/components/Logo'));
interface Props {
  onChangeScene: (scene: I_Scene) => void;
}

const ListScene = memo(({ onChangeScene }: Props) => {
  const { checkDevice } = useMobileDetect();

  const listGroup = useSelector(selectListGroup);
  const sceneSelected = useSelector(selectSceneSelected);

  const groupActive = useMemo(
    () =>
      listGroup.filter(item => sceneSelected?.group === item.id)[0]?.id ||
      'uncategory',
    [listGroup, sceneSelected],
  );

  return (
    <HeaderWrapper>
      <Logo />
      <div className="--scenes">
        {listGroup.map(group =>
          checkDevice().isMobile() ? (
            <div
              key={group.id}
              className={`dropdownGroup show-on-mobile ${
                groupActive === group.id ? 'active' : ''
              }`}
            >
              <Popover
                zIndex={99}
                content={
                  <div className="dropdownList">
                    <div className="--listing">
                      {group.scenes.map(scene => (
                        <button
                          key={scene.id}
                          type="button"
                          onClick={e => {
                            e.stopPropagation();
                            onChangeScene(scene);
                          }}
                          className={`${
                            sceneSelected?.id === scene.id ? 'active' : ''
                          }`}
                        >
                          <h2 className="--menuSceneTitle">{scene.title}</h2>
                        </button>
                      ))}
                    </div>
                  </div>
                }
                overlayClassName="popoverSceneList"
                trigger="click"
              >
                <button type="button" className="dropdownLabel">
                  <i aria-hidden className="fal fa-plus" />
                  <span>
                    {group.name === 'Uncategory' ? 'Uncategory' : group.name}
                  </span>
                </button>
              </Popover>
            </div>
          ) : (
            <div
              key={group.id}
              className={`dropdownGroup hide-on-mobile ${
                groupActive === group.id ? 'active' : ''
              }`}
              onClick={() => onChangeScene(group.scenes[0])}
            >
              <button type="button" className="dropdownLabel">
                <i aria-hidden className="fal fa-plus" />
                <span>
                  {group.name === 'Uncategory' ? 'Uncategory' : group.name}
                </span>
              </button>
              <div className="dropdownList">
                <div className="--listing">
                  {group.scenes.map(scene => (
                    <button
                      key={scene.id}
                      type="button"
                      onClick={e => {
                        e.stopPropagation();
                        onChangeScene(scene);
                      }}
                      className={`${
                        sceneSelected?.id === scene.id ? 'active' : ''
                      }`}
                    >
                      <h2 className="--menuSceneTitle">{scene.title}</h2>
                    </button>
                  ))}
                </div>
              </div>
            </div>
          ),
        )}
      </div>
    </HeaderWrapper>
  );
});

export default ListScene;
